.empty {
    padding: 30px;
    border-radius: 20px;
    display: flex;
    align-items: center;
    flex-direction: column;
    &__title {
        font-family: Lobster;
        font-size: 40px;
        text-align: center;
        margin-bottom: 50px;
    }
    &__imgbox {
        display: flex;
        align-items: center;
        justify-content: center;
        margin-bottom: 50px;
    }
    &__svg {
        max-width: 400px;
        width: 100%;
        fill: #b2e5fb;
    }
}
